import { Col } from 'antd';
import VideoCard from "./VideoCard";
import React from "react";
import './Cards.css'
const Cards = ({videosArray}) =>{
    return (
        <>
            {videosArray.map((video,index)=>(
                <Col span={20} key={index} className="cards">
                    <VideoCard id = {video['_id']}title={video['title']} thumbnailUrl = {video['thumbnails']['medium']['url']} description = {video['description']}  channelTitle={video['channelTitle']} views={video['views']}>
                    </VideoCard>
                </Col>
            ))}
        </>
    )
}

export default Cards;