
import React from 'react';
import 'antd/dist/antd.css';
import { Row, Col} from 'antd';
import {CheckCircleOutlined} from '@ant-design/icons';
import { useHistory } from "react-router";




const VideoCard = ({title,thumbnailUrl,channelTitle,views,description,id})=>{
    const history = useHistory();
    const limit = 300;
    const len =  description.length;
    const printDescription = ()=>{
        if(len > limit){
            const ans = description.substring(0,limit) + ' ...';
            return ans;
        }
        else
            return description;
    }
    const viewString = ()=>{
        const a = parseInt(views);
        let ans = "";
        if(a >= 10000000 ){
            ans = views.substring(0,2)+'M';
        }
        else if(a >= 1000000){
            if(views[1] != 0){
                ans = views[0]+'.'+views[1]+'M';
            }
            else{
                ans = views[0]+'M';
            }
        }
        else if(a >= 100000){
            ans = views.substring(0,3)+'K';
        }
        else{
            ans = views.substring(0,2)+'K';
        }

        return ans;
    }

    const videoPage = ()=>{
        history.push(`/video/?id=${id}`);
    }
    return(
        <a onClick={videoPage}>
           <Row gutter={[0,0]}>
               <Col span={9}>
                   <img src={thumbnailUrl}></img>
               </Col>
               <Col span={15}>
                   <Row>
                       <Col span={24}>
                            <h3>{title}</h3>
                       </Col>
                       <Col span={24} >
                            <h5 style={{color:'#606060'}}>{channelTitle +' '} <i> <CheckCircleOutlined /> </i>{'  '+ viewString() + ' views'}</h5>
                       </Col>
                       <Col span={24}>
                            <h5 style={{color:'#606060'}}>{printDescription()}</h5>
                       </Col>
                   </Row>
                   
               </Col>
           </Row>
        </a>   
    );
};


export default VideoCard;

