import React, { useEffect, useState } from "react";
import { Card, Avatar,Divider} from 'antd';
import Header from "./Header";
import {useLocation} from "react-router-dom";
import './Home.css';
import './Video.css';
import YouTube from "react-youtube";
import {LikeFilled} from '@ant-design/icons';
const { Meta } = Card;


const Video = ()=>{
    const search = useLocation().search;
    const id = new URLSearchParams(search).get('id');
    const [toggled, setToggled]=useState(false)
    const width = window.innerWidth*0.95
    const opts = {
        width: width,
        height: width*10/24,
        playerVars: {
            autoplay: 1,
        },
    };
    const [details,setDetails] = useState();
    const [loading,setLoading] = useState(false);
    useEffect(async()=>{
        setLoading(true);
        
        const endpointUrl = `https://youtubetrendinglist.herokuapp.com/videoDetails?id=${id}`;
        const rawData = await fetch(endpointUrl);
        const jsonData = await rawData.json();
        setDetails(jsonData);
        setLoading(false);
        console.log(jsonData);

    },[]);

    function countString(){
        if(!details?.channelData?.subscriberCount ){
            return "Subscriber Count Hidden";
        }
        const a = parseInt(details?.channelData?.subscriberCount);
        const subs = details?.channelData?.subscriberCount;
        let ans = "";
        if(a >= 10000000 ){
            ans = subs.substring(0,2)+'M subscribers';
        }
        else if(a >= 1000000){
            if(subs[1] != 0){
                ans = subs[0]+'.'+subs[1]+'M subscribers';
            }
            else{
                ans = subs[0]+'M subscribers';
            }
        }
        else if(a >= 100000){
            ans = subs.substring(0,3)+'K subscribers';
        }
        else{
            console.log(subs);
            ans = subs[0]+subs[1]+'K subscribers';
        }

        return ans;
    }

    function numberWithCommas(x) {
        if(!x)
            return '0';
       let  a= x.replace(/\B(?=(\d{3})+(?!\d))/g, ",");
       return a;
       
    }
   
    

    return (
        <>
            <Header refresh={false}/>
            <div className="home-container">
                <div className="player">
                    {((loading)?(<><Card style={{width:'100%',height:'500px'}} loading={true}></Card></>):(<><Card cover={<YouTube videoId={id} opts={opts}/>}>
                        <h2>{details?.videoData?.title}</h2>
                        <div className="stats">
                            <h3>{numberWithCommas(details?.videoData?.views)+' views'}</h3>
                            <h3><i><LikeFilled /></i> {numberWithCommas(details?.videoData?.likes) }</h3>
                        </div>
                        
                        <Divider orientation="center" plain></Divider>
                         <Meta avatar={<Avatar size={64} src={details?.channelData?.thumbnails?.default.url}/>}  title={details?.channelData?.title} description={countString()}/> 
                         <Divider orientation="center" plain></Divider>
                         <h3>Video Description</h3>
                            <div className={`videoDesc ${toggled?"full":"half"}`}
                            dangerouslySetInnerHTML={{__html: details?.videoData?.description.replaceAll("\n","<br/>")}}>
                            </div>
                            <a style={{color:'#060606'}} onClick={()=>{
                            setToggled(!toggled)
                            }}>Show {toggled?"Less":"More"}</a>
                    </Card></>))}

                </div>
            </div>
        </>
    )
}

export default Video;

