import Header from "./Header";
import React, { useEffect, useState } from "react";
import { Row, Divider,Card,Col } from 'antd';
import Cards from "./Cards";
import "./Home.css";
import Foot from "./Foot";



const Home =  (props) =>{
    const [trendingVideos, setTrendingVideos] = useState([]);
    const [loading,setLoading] = useState(true);
    useEffect(()=>{
         async function fetchData( ){
            setLoading(true);
            const trendingVideosEndPoint = 'http://localhost:8000/videos';
            const rawData = await fetch(trendingVideosEndPoint);
            const videosData = await rawData.json();
            // console.log(videosData);
            setTrendingVideos(videosData);
            setLoading(false);
        };
        fetchData();
        
    },[]);

    const loadingCard = (number)=>{
        let arr = [];
        for(let i = 0;i < number; i++){
            arr.push(<Col span={20} key={i+1}><Card loading={true}></Card></Col>);
        }
        return arr;
    }
    return (
        <>
            
            <Header refresh={true}/>
            <div className="home-container">
                <Divider orientation="center" plain><h3>Trending Videos</h3></Divider>
                <Row className="news-box" gutter={[16,32]}>
                    {((loading)?(
                        <>
                            {loadingCard(4)}
                        </>
                    ):(<Cards videosArray={trendingVideos}/>))}
                    
                </Row>
            </div>
            <Foot/>
        </>
        
    );
}

export default Home;

